'use strict';

// MODULES //

import sqrtf from '@stdlib/math-base-special-sqrtf';
import f32 from '@stdlib/number-float64-base-to-float32';


// MAIN //

/**
* Computes the hypotenuse of two single-precision floating-point numbers.
*
* @param {number} x - number
* @param {number} y - number
* @returns {number} hypotenuse
*
* @example
* var h = hypotf( -5.0, 12.0 );
* // returns 13.0
*/
function hypotf( x, y ) {
	x = f32( x );
	y = f32( y );
	return sqrtf( f32( f32( x * x ) + f32( y * y ) ) );
}


// EXPORTS //

export default hypotf;
